import React, { memo } from 'react'
import DefaultTemplate from '../template'
import type { TemplateProps } from '../template'
import type { KcProps } from 'keycloakify'
import type { KcContextBase } from 'keycloakify'
import type { I18n } from '../../i18n'

export type ILoginPageExpired = KcProps & {
  kcContext: KcContextBase.LoginPageExpired
  i18n: I18n
  doFetchDefaultThemeResources?: boolean
  Template?: (props: TemplateProps) => JSX.Element | null
}

const LoginPageExpired = memo((props: ILoginPageExpired) => {
  const {
    kcContext,
    i18n,
    doFetchDefaultThemeResources = true,
    Template = DefaultTemplate,
    ...kcProps
  } = props

  const { url } = kcContext

  const { msg } = i18n

  return (
    <Template
      {...{ kcContext, i18n, doFetchDefaultThemeResources, ...kcProps }}
      displayMessage={false}
      headerNode={msg('pageExpiredTitle')}
      formNode={
        <>
          <p id='instruction1' className='instruction'>
            {msg('pageExpiredMsg1')}
            <a id='loginRestartLink' href={url.loginRestartFlowUrl}>
              {msg('doClickHere')}
            </a>{' '}
            .<br />
            {msg('pageExpiredMsg2')}{' '}
            <a id='loginContinueLink' href={url.loginAction}>
              {msg('doClickHere')}
            </a>{' '}
            .
          </p>
        </>
      }
    />
  )
})

export default LoginPageExpired
